import React from "react"
import { Link } from "gatsby"
import { Box, Text, Heading } from "@chakra-ui/core"
import theme from "../gatsby-plugin-chakra-ui/theme"

const Posts = ({posts}) => {
  return (
    <Box color={theme.colors.white}>
      {posts.map(({ node }) => {
        const title = node.frontmatter.title || node.fields.slug
        return (
          <Link key={node.fields.slug} style={{ boxShadow: `none` }} to={node.fields.slug}>
            <Box borderWidth="1px" backgroundColor={theme.colors.black} mb="10px" mr="10px" ml="10px" padding="10px" borderColor={theme.colors.white} boxShadow={`2px 2px 0px ${theme.colors.white}}`} borderRadius="5px">
              <article key={node.fields.slug}>
                <header>
                  <Heading as="h3" fontSize="lg">
                    {title}
                  </Heading>
                  <Text fontSize="xs" mb="5px">{`${node.timeToRead} minutes to read`}</Text>
                  <Text fontSize="xs">{node.frontmatter.date}</Text>
                </header>
                <section>
                  <Text as="p" fontSize="md"
                    dangerouslySetInnerHTML={{
                      __html: node.frontmatter.description || node.excerpt,
                    }}
                  />
                </section>
              </article>
            </Box>
          </Link>
        )
      })}
    </Box>

  )
}

export default Posts
